<template>
    <div class="header-container">
        <div l-header></div>
        <!--  -->
        <div r-header>
            <el-dropdown @command="handleClick">
                <span class="el-dropdown-link">
                    <img :src="imgheader" class="userImg" alt="" />
                </span>
                <el-dropdown-menu>
                    <!-- <el-dropdown-item >个人中心</el-dropdown-item> -->
                    <el-dropdown-item @click.native.prevent="handleClick"
                        >切换账号</el-dropdown-item
                    >
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<style lang="less" scoped>
.header-container {
    padding: 0 20px;
    //background-color: #333;

    background-image: linear-gradient(
        to right,
        #409eff,
        rgba(237, 246, 243, 0.977)
    );

    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .userImg {
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }
}
</style>

<script>
export default {
    data() {
        return {
            imgheader: "",
        };
    },
    methods: {
        handleClick() {
            sessionStorage.removeItem("token");
            this.$router.push("/login");
        },
    },
    mounted() {
        let token = sessionStorage.getItem("token");
        console.log(token);
        if (token == "user") {
            this.imgheader = require("../assets/1.jpg");
        } else if (token == "admin") {
            this.imgheader = require("../assets/plr.png");
        }
    },
};
</script>