</template>
<script>
import bus from "../plugins/eventBus";
export default {
    data() {
        return {
            show: false,
            dataList: [],
            options: {
                // licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
                // 是否显示导航，默认为false
                sectionsColor: ["", "", "", ""],
                anchors: ["page1", "page2", "page3", "page4"], // 注意不带#
                menu: "#menu", // 绑
                recordHistory: false,
            },
            menuList: [
                { label: "苏菜", value: "page1" },
                { label: "川菜", value: "page2" },
                { label: "鲁菜", value: "page3" },
                { label: "粤菜", value: "page4" },
            ],
           
        };
    },
    computed: {
        SuFood() {
            //console.log(this.$store.state.food.SuFood);
            return this.$store.state.food.SuFood;
        },
        chuanFood() {
            return this.$store.state.food.chuanFood;
        },
        yueFood() {
            return this.$store.state.food.yueFood;
        },
        luFood() {
            return this.$store.state.food.luFood;
        },
        //苏菜金额总计
        totalPrice() {
            var sum = 0;
            this.SuFood.forEach((v) => {
                sum += v.price * v.count;
            });
            return sum;
        },
        //川菜金额总计
        totalPrice1() {
            var sum = 0;
            this.chuanFood.forEach((v) => {
                sum += v.price * v.count;
            });
            return sum;
        },
        //鲁菜金额总计
        totalPrice2() {
            var sum = 0;
            this.luFood.forEach((v) => {
                sum += v.price * v.count;
            });
            return sum;
        },
        //粤菜金额总计
        totalPrice3() {
            var sum = 0;
            this.yueFood.forEach((v) => {
                sum += v.price * v.count;
            });
            return sum;
        },
        //菜品数量总计
        total1() {
            var sum = 0;
            this.SuFood.forEach((v) => {
                sum += v.count;
            });
            return sum;
        },
        total2() {
            var sum = 0;
            this.chuanFood.forEach((v) => {
                sum += v.count;
            });
            return sum;
        },
        total3() {
            var sum = 0;
            this.luFood.forEach((v) => {
                sum += v.count;
            });
            return sum;
        },
        total4() {
            var sum = 0;
            this.yueFood.forEach((v) => {
                sum += v.count;
            });
            return sum;
        },
        total() {
            return this.total1 + this.total2 + this.total3 + this.total4;
        },
    },

    mounted() {
        this.$refs.fullpage.api.setLockAnchors(true);
    },
    methods: {
        //支付,全局事件总线
        pay() {
            setTimeout(() => {
                bus.$emit(
                    "getPay",
                    this.totalPrice,
                    this.totalPrice1,
                    this.totalPrice2,
                    this.totalPrice3
                );
            }, 10);
            
            this.$router.push("/Pay");
        },
        //返回主页
        goback() {
            this.$router.push("/Home");
        },
        handleMove(index) {
            switch (index) {
                case 0:
                    this.$refs.fullpage.api.moveTo(1, 0);
                    break;
                case 1:
                    this.$refs.fullpage.api.moveTo(2, 1);
                    break;
                case 2:
                    this.$refs.fullpage.api.moveTo(3, 2);
                    break;
                case 3:
                    this.$refs.fullpage.api.moveTo(4, 3);
                    break;
            }
        },
        //苏菜按钮
        increase(i) {
            console.log(this.SuFood[i].name);
            this.SuFood[i].count++;
            console.log(this.total);
        },
        decrease(i) {
            this.SuFood[i].count--;
        },
        //川菜加减按钮
        increase1(i) {
            console.log("这是川菜");
            this.chuanFood[i].count++;
        },
        decrease1(i) {
            this.chuanFood[i].count--;
        },
        //鲁菜加减按钮
        increase2(i) {
            console.log("这是鲁菜");
            this.luFood[i].count++;
        },
        decrease2(i) {
            this.luFood[i].count--;
        },
        //粤菜加减按钮
        increase3(i) {
            console.log("这是粤菜");
            this.yueFood[i].count++;
        },
        decrease3(i) {
            console.log("这是粤菜");
            this.yueFood[i].count--;
        },
    },
};
</script>
