<template>
    <div class="login_container">
        <div class="login_box">
            <!--头像区域-->
            <div class="avatar_box">
                <img src="../assets/plr.png" alt="" />
            </div>
            <!--登录表单区域-->
            <el-form
                ref="loginFormRef"
                :model="loginForm"
                :rules="loginFormRules"
                label-width="0px"
                class="login_from"
            >
                <!-- 用户名 -->
                <el-form-item prop="username">
                    <el-input
                        v-model="loginForm.username"
                        prefix-icon="el-icon-gerenzhongxin"
                    >
                    </el-input>
                </el-form-item>
                <!-- 密码 -->
                <el-form-item prop="password">
                    <el-input
                        v-model="loginForm.password"
                        prefix-icon="el-icon-jiemi"
                        type="password"
                    >
                    </el-input>
                </el-form-item>
                <!-- 按钮区域 -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="primary" @click="resetLoginForm"
                        >重置</el-button
                    >
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            //这是登录表单数据绑定
            loginForm: {
                username: "user",
                password: "123456",
            },
            //表单验证规则对象
            loginFormRules: {
                //用户名是否合法
                username: [
                    {
                        required: true,
                        message: "请输入用户名",
                        trigger: "blur",
                    },
                    {
                        min: 3,
                        max: 5,
                        message: "长度在 3 到 5 个字符",
                        trigger: "blur",
                    },
                ],
                //密码是否合法
                password: [
                    {
                        required: true,
                        message: "请输入登陆密码",
                        trigger: "blur",
                    },
                    {
                        min: 6,
                        max: 15,
                        message: "长度在 6 到 15 个字符",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        // 点击重置按钮，重置登录表单
        resetLoginForm() {
            //console.log(this);
            this.$refs.loginFormRef.resetFields();
        },
        login() {
            let token = "user";
            let admin = "admin";
            
            //debugger;
            if (
                this.loginForm.username == "admin" &&
                this.loginForm.password == "123456"
            ) {
                sessionStorage.setItem("token", admin);
                this.$message.success("登录成功");
                this.$router.push("/home");
            } else if (
                this.loginForm.username == "user" &&
                this.loginForm.password == "123456"
            ) {
                sessionStorage.setItem("token", token);
                this.$message.success("登录成功");
                this.$router.push("/home");
            } else {
                this.$message.error("用户名或者密码错误");
            }
        },
    },
};
</script>


<style lang="less" scoped>
.login_container {
    background: url("../assets/background.png");
    height: 100%;
    width: 100%;
    position: fixed;
    background-size: 100% 100%;
}

.login_box {
    width: 450px;
    height: 300px;
    background-color: rgba(32, 133, 248, 0.1);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .avatar_box {
        height: 130px;
        width: 130px;
        border: 1px solid #eee;
        border-radius: 50%;
        padding: 10px;
        box-shadow: 0 0 10px #fff;
        position: absolute;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #fff;
        img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background-color: #fff;
        }
    }
}

.login_from {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}

.btns {
    display: flex;
    justify-content: flex-end;
}
</style>
