import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/view/Login.vue'
import Main from '@/components/Main.vue'
import Shop from '../view/Shop.vue'
import Pay from '../view/Pay.vue'
import Home from '../view/Home.vue'


const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)



//const Main = () => import('../view/Main.vue')
//const Pay = () => import('../view/Pay.vue')
export default new VueRouter({
  routes: [
    { path: '/', redirect: '/login', },
    { path: '/login', component: () => import('../view/Login.vue'), name: "login" },

    {
      path: '/main',
      component: () => import('@/components/Main.vue'),
      children: [
        { path: '/shop', component: () => import('@/view/Shop.vue'), name: 'shop' },
        { path: '/home', component: () => import('@/view/Home.vue'), name: 'home' },
        { path: '/myform', component: () => import('@/view/myForm.vue'), name: 'myform' },
        { path: '/useManage', component: () => import('@/view/User.vue'), name: 'usemanage' },
        { path: '/menuManage', component: () => import('@/view/menuManagement.vue'), name: 'menumanage' },
        
      ]
    },

    { path: '/pay', component: () => import('@/view/Pay.vue') },


  ]
})


