
export default {
    state: {
        order: true,
        formList: {

        },
        tableData: [
            {
                id: 1,
                name: "王小虎",
                age: 25,
                phoneNumber: "123456789",
                sex: 0
            },
            {
                id: 2,
                name: "张龙",
                age: 20,
                phoneNumber: "123456789",
                sex: 0
            },
            {
                id: 3,
                name: "李铁柱",
                age: 42,
                phoneNumber: "123456789",
                sex: 1
            },
            {
                id: 4,
                name: "钱四海",
                age: 32,
                phoneNumber: "123456789",
                sex: 0
            },
            {
                id: 5,
                name: "丈裕清",
                age: 35,
                phoneNumber: "123456789",
                sex: 1
            },
        ],
    },
    mutations: {
        addList(state, val) {
            state.formList.push(val)
        },
        changeBtn() {
            state.order = !state.order
        },
        addUser(state, val) {
            state.tableData.push(val)
        },
        userDel(state, val) {
            state.tableData.forEach((item, i) => {
                //console.log(item.name==val,i);
                if (item.name == val) {
                    state.tableData.splice(i, 1)
                }
            });
        },
        userEdit(state, val,) {
            state.tableData = val
            //console.log(state.tableData);

        }
    }
}