<template>
    <div>
        <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            
            text-color="#666"
            active-text-color="skyblue"
        >
            <el-menu-item index="1" @click="handleHome">
                <i class="el-icon-s-shop"></i>
                <span slot="title">首页</span>
            </el-menu-item>

            <!--  -->
            <el-menu-item index="4" @click="handleFood">
                <i class="el-icon-dish"></i>
                <span slot="title">点餐</span>
            </el-menu-item>
            <!--  -->
            <el-menu-item index="3" @click="handleMy" v-show="order">
                <i class="el-icon-shopping-cart-1"></i>
                <span slot="title">我的订单</span>
            </el-menu-item>
            <el-menu-item index="5" @click="handleUser" v-show="orderUser">
                <i class="el-icon-user"></i>
                <span slot="title">用户管理</span>
            </el-menu-item>
            <el-menu-item index="6" @click="handleMenu" v-show="menus">
                <i class="el-icon-tableware"></i>
                <span slot="title">菜单管理</span>
            </el-menu-item>
            <!--  -->
            <!-- <el-submenu index="2" >
                <template slot="title">
                    <i class="el-icon-more-outline"></i>
                    <span>其他</span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="1-1">
                        联系商家</el-menu-item>
                    <el-menu-item index="1-2">加盟通道</el-menu-item>
                </el-menu-item-group>
            </el-submenu> -->
        </el-menu>
    </div>
</template>

<style lang="less" scoped>
.el-menu {
    height: 100vh;
    border-right: none;
    /deep/.el-menu-item:hover {
        background-color: #409eff !important;
    }
    /deep/.el-submenu__title:hover {
        background-color: #409eff !important;
    }
}
.el-menu-vertical-demo {
   background: #dce4ec;
   //border-radius: 12px;
}
</style>
<script>
import store from "@/store";
export default {
    data() {
        return {
            order: true,
            orderUser: true,
            menus: true,
        };
    },
    methods: {
        handleFood() {
            this.$router.push("/shop");
        },
        handleHome() {
            this.$router.push("home");
        },
        handleMy() {
            this.$router.push("myform");
        },
        handleUser() {
            this.$router.push("/usemanage");
        },
        handleMenu() {
            this.$router.push("menumanage");
        },
    },
    mounted() {
        let token = sessionStorage.getItem("token");
        console.log(token);
        if (token == "user") {
            this.order = true;
            this.orderUser = false;
            this.menus = false;
        }else if(token == "admin"){
             this.order = false;
            this.orderUser = true;
            this.menus = true;
        }
    },
};
</script>