_c('el-tab-pane',{attrs:{"label":"粤菜"}},[_c('p',[_vm._v(" 粤菜即广东菜，是中国四大菜系、八大菜系之一。狭义上的粤菜指广府菜（即广州府菜），广义上又包含潮州菜（潮汕菜）、东江菜（也称客家菜） ")]),_c('p',[_vm._v(" 粤菜源自中原，传承了孔子所倡导的“食不厌精，脍不厌细”的中原饮食风格 ，因此粤菜做法比较复杂、精细，如广府菜中的煲仔饭、烤乳猪源自周代“八珍”美食；烧鹅源自宋朝名菜烤鸭；点心从中原传到广东后演变出虾饺、干蒸烧卖等广式点心。 ")]),_c('p',[_vm._v(" 广府菜范围包括珠江三角洲和韶关等地，广府菜是粤菜的代表，自古有“食在广州，厨出凤城(顺德)”、“食在广州，味在西关”的美誉 ， 潮州菜发源于广东潮汕地区，潮菜是粤菜的主干与粤菜的代表，也有“食在广州、味在潮州”的说法。 广东客家菜主要流行在梅州、惠州、河源、韶关、深圳等地，范围包括梅江、东江和北江流域。 高凉菜属于粤西菜式源头之一，发源于古代高凉郡，包括粤西地区茂名、阳江等地。湛江地区的代表菜式是湛江菜。 ")])]),_c('el-tab-pane',{attrs:{"label":"鲁菜"}},[_c('p',[_vm._v(" 鲁菜，是中国传统四大菜系（也是八大菜系）中的自发型菜系（相对于淮扬、川、粤等影响型菜系而言），是历史最悠久、技法最丰富、最见功力的菜系。是黄河流域烹饪文化的代表。 ")]),_c('p',[_vm._v(" 2500年前山东的儒家学派奠定了中国饮食注重精细、中和、健康的审美取向；北魏末年《齐民要术》（成书时间为约公元533—544年）总结的黄河中下游地区的“蒸、煮、烤、酿、煎、炒、熬、烹、炸、腊、盐、豉、醋、酱、酒、蜜、椒”奠定了中式烹调技法的框架；明清时期大量山东厨师和菜品进入宫廷，使鲁菜雍容华贵、中正大气、平和养生的风格特点进一步得到升华。 ")]),_c('p',[_vm._v(" 经典菜品有一品豆腐、糖醋鲤鱼、葱烧海参、三丝鱼翅、白扒四宝、糖醋黄河鲤鱼、九转大肠、油爆双脆、扒原壳鲍鱼、油焖大虾、醋椒鱼、糟熘鱼片、温炝鳜鱼片、芫爆鱿鱼卷、清汤银耳、木樨肉（木须肉）等 ")])]),_c('el-tab-pane',{attrs:{"label":"苏菜"}},[_c('p',[_vm._v(" 江苏菜，中国汉族八大菜系之一 ，简称苏菜。由于苏菜和浙菜相近，因此和浙菜统称江浙菜系。主要以金陵菜、淮扬菜、苏锡菜、徐海菜等地方菜组成。江苏菜起源于二千多年前，其中金陵菜起源于先秦时期，当时吴人善制炙鱼、蒸鱼和鱼片，一千多年前，鸭已为南京美食。南宋时，苏菜和浙菜同为“南食”的两大台柱。 ")]),_c('p',[_vm._v(" 苏菜擅长炖、焖、蒸、炒，重视调汤，保持菜的原汁，风味清鲜，浓而不腻，淡而不薄，酥松脱骨而不失其形，滑嫩爽脆而不失其味。 ")]),_c('p',[_vm._v(" 金陵菜口味和醇，玲珑细巧；徐州菜色调浓重，习尚五辛；扬州菜清淡适口，刀工精细；苏州菜口味趋甜，清雅多姿。其名菜有金陵烤鸭、彭城鱼丸、老鸭汤、炖生敲、烤方、羊方藏鱼、水晶肴蹄、清炖蟹粉狮子头、霸王别姬、黄泥煨鸡、清炖鸡孚、盐水鸭、金陵板鸭、鼋汁狗肉、金香饼、鸡汤煮干丝、肉酿生麸、红烧沙光鱼、凤尾虾、三套鸭、无锡肉骨头、陆稿荐酱猪头肉等。 ")])])],1)],1)])
