<template>
    <div class="pay">
        <el-col :span="10">
            <el-card shadow="hover" style="width: 100%">
                <div slot="header" class="clearfix">
                    <span class="text">剩余时间</span>
                </div>

                <!-- 倒计时 -->
                <el-statistic
                    ref="statistic"
                    @finish="hilarity"
                    format="HH:mm:ss"
                    :value="deadline3"
                    time-indices
                >
                </el-statistic>
            </el-card>
        </el-col>

        <span class="textPay">待支付:{{ a + b + c + d }}元 </span>

        <el-input
            type="text"
            placeholder="请输入评分"
            class="grade"
            v-model="grade"
        ></el-input>

        <div class="paydet">
            <el-button type="primary" @click="confirm">确定</el-button>
            <el-button @click="goback1">取消</el-button>
        </div>
    </div>
</template>
<script>
import bus from "../plugins/eventBus.js";
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";
import axios from "axios";
import { watch } from 'vue';
export default {
    data() {
        return {
            a: "",
            b: "",
            c: "",
            d: "",
            total: "", //金额
            nowDate: "", //日期
            id: "", //序号
            table: "", //订单
            grade: "", //评分
            format: "HH小时:mm:ss:SSS",
            deadline: Date.now() + 1000 * 60 * 60 * 24 * 2,
            deadline2: Date.now() + 1000 * 60 * 60 * 8,
            deadline3: Date.now() + 500 * 60 * 30,
            deadline4:
                Date.now() + (new Date().setHours(23, 59, 59) - Date.now()),
        };
    },
    computed: {},
    mounted() {
        //需要借助一个暂放空间，将回调函数(既箭头函数)的值放到里
        //使用this.$parent将组件$on获取的接收到的值放入父组件data，再从父组件中取出$on的传值
        bus.$on("getPay", (a, b, c, d) => {
            console.log(a);
            console.log(b);
            console.log(c);
            console.log(d);
            this.a = a;
            this.b = b;
            this.c = c;
            this.d = d;
        });
         this.getOrder()
    },
    methods: {
        hilarity() {
            this.$notify({
                title: "提示",
                message: "时间已到，你可知寸金难买寸光阴？",
                duration: 0,
            });
        },

        clickFn() {
            this.$refs.statistic.suspend(this.stop);
            this.stop = !this.stop;
        },
        add() {
            this.deadline3 = this.deadline3 + 1000 * 10;
        },
        date() {
            var myDate = new Date(); //创建Date对象
            var Y = myDate.getFullYear(); //获取当前完整年份
            var M = myDate.getMonth() + 1; //获取当前月份
            var D = myDate.getDate(); //获取当前日1-31
            if (M < 10) {
                M = "0" + M;
            }
            if (D < 10) {
                D = "0" + D;
            }
            //console.log(Y, M, D);
            return (this.nowDate = Y + "-" + M + "-" + D);
        },
        getOrder() {
            axios
                .get("http://127.0.0.1/order/get")
                .then((res) => {
                    //console.log(res.data);
                    this.table = res.data;
                })
                .catch((err) => {
                    console.log("获取数据失败" + err);
                });
        },
        //添加订单
        addOrder() {
            axios
                .get("http://127.0.0.1/order/add", {
                    params: {
                        id: this.id,
                        total: this.total + "元",
                        date: this.nowDate,
                        grade: this.grade,
                    },
                })
                .then((res) => {
                    if (res.data.status == 200) {
                    } else {
                        // this.$message({
                        //     message: "添加失败",
                        //     type: "error",
                        // });
                    }
                })
                .catch((err) => {
                    console.log("操作失败" + err);
                });
        },
        //支付操作
        confirm() {
           
            this.$message({
                message: "成功支付",
                type: "success",
            });
            this.date(); //获取当前时间
            this.getOrder(); //获取当前序号
            this.total = this.a + this.b + this.c + this.d;//获取金额
            console.log(this.table.length);
            
            this.id=this.table.length+1//获取当前序号
            if(this.id>this.table.length){
                 this.addOrder(); //执行添加操作
            }
           
            this.$router.push("/Home");
           
        },
        goback1() {
            this.$router.push("/Shop");
        },
    },
    computed: {
        ...mapState({ img: "img" }),
    },
 
    
};
</script>
<style scoped lang="less">
/deep/ .el-col {
    position: relative;
    top: 5px;
    left: 5px;
}
.paydet {
    position: relative;
    width: 150px;
    height: 70px;
    left: 420px;
    top: 100px;
}
.pay {
    position: relative;
    margin: 80px auto;
    background: white;
    width: 600px;
    height: 400px;
    border-radius: 10px;
    border: 2px solid #409eff;
    /deep/.el-input__inner {
        width: 200px;
    }
}
.textPay {
    position: relative;
    margin: 80px auto;
    display: table;

    font-size: 30px;
    top: 0px;
    left: 60px;
}
.like {
    cursor: pointer;
    font-size: 25px;
    display: inline-block;
}
</style>